body {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

html {
  --color-blue: #00acdc;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
}

.logo, .nav, .nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-align: left;
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  transition: all .25s;
  display: flex;
}

.nav__link:hover, .is-active {
  color: var(--color-blue);
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
